import {urlParam} from "./encoder";

function assign(p) {
    let c;
    if ((typeof p === 'object' && p != null)) {
        if ((Array.isArray(p)))
            c = [];
        else
            c = {};
        for (var i in p) {
            if (typeof p[i] === 'object' && !i.indexOf("_") != 0) {
                c[i] = this.assign(p[i]);
            } else {
                c[i] = p[i];
            }
        }
    } else {
        c = p;
    }
    return c;
}

function getErrorText(error) {
    if (typeof (error) == "string") {
        try {
            let errorMessage = JSON.parse(error);
            return errorMessage.errorMsg || error;
        } catch (e) {
            return error;
        }
    } else {
        return JSON.stringify(error);
    }
}

export class APIException {
    status = 0;
    error = "";

    constructor(status, error) {
        this.status = status;
        this.error = error
    }
}

export class APIInvoker {
    constructor(apiHost, defaultHeaders, isCompress) {
        this.beforeFunctionList = [];
        this.afterFunctionList = [];
        this.onSuccess = undefined;
        this.onError = undefined;
        this.apiHost = undefined;
        this.method = undefined;
        this.endPoint = undefined;
        this.data = undefined;
        this.dataType = undefined;
        this.defaultHeaders = undefined;
        this.methodMap = {
            0: "HEAD",
            1: "GET",
            2: "DELETE",
            3: "POST",
            4: "PUT",
            5: "PATCH"
        };
        this.apiHost = apiHost;
        this.defaultHeaders = defaultHeaders || {};
        this.isCompress = isCompress;
    }

    api(endPoint) {
        this.endPoint = endPoint;
        return this;
    };

    success(func) {
        this.onSuccess = func;
        return this;
    };

    error(func) {
        this.onError = func;
        return this;
    };

    head() {
        this.method = 0;
        this.exec();
    };

    get() {
        this.method = 1;
        this.exec();
    };

    delete() {
        this.method = 2;
        this.exec();
    };

    post(data, dataType) {
        this.method = 3;
        this.data = data || {};
        this.dataType = dataType || APIInvoker.dataTypeMap.urlForm;
        this.exec();
    };

    put(data, dataType) {
        this.method = 4;
        this.data = data || {};
        this.dataType = dataType || APIInvoker.dataTypeMap.urlForm;
        this.exec();
    };

    patch(data, dataType) {
        this.method = 5;
        this.data = data || {};
        this.dataType = dataType || APIInvoker.dataTypeMap.urlForm;
        this.exec();
    };

    async headAsync() {
        this.method = 0;
        return this.execAsync();
    };

    async getAsync() {
        this.method = 1;
        return this.execAsync();
    };

    async deleteAsync() {
        this.method = 2;
        return this.execAsync();
    };

    async postAsync(data, dataType) {
        this.method = 3;
        this.data = data || {};
        this.dataType = dataType || APIInvoker.dataTypeMap.urlForm;
        return this.execAsync();
    };

    async putAsync(data, dataType) {
        this.method = 4;
        this.data = data || {};
        this.dataType = dataType || APIInvoker.dataTypeMap.urlForm;
        return this.execAsync();
    };

    async patchAsync(data, dataType) {
        this.method = 5;
        this.data = data || {};
        this.dataType = dataType || APIInvoker.dataTypeMap.urlForm;
        return this.execAsync();
    };

    before(func) {
        if (func instanceof Function)
            this.beforeFunctionList.unshift(func);
        return this;
    }

    after(func) {
        if (func instanceof Function)
            this.afterFunctionList.unshift(func);
        return this;
    }

    exec() {
        this.execAsync().then((response) => {
            this.onSuccess && this.onSuccess(response.status, response);
        }).catch(({status, error}) => {
            if (this.onError != undefined)
                this.onError(status, getErrorText(error));
            else
                console.log(status, error);
        });
    };

    execAsync() {
        let context = this;
        return new Promise((resolve, reject) => {
            let headers = new Headers(context.defaultHeaders);
            let request = {
                method: context.methodMap[context.method],
                mode: "cors"
            };
            let latestData = context.data;
            if (context.method > 2) {
                headers.append("Content-Type", context.dataType);
                if (context.dataType == APIInvoker.dataTypeMap.urlForm && context.data instanceof Object) {
                    latestData = urlParam(context.data);
                }
                if (context.isCompress) {
                    // request["body"] = pako.gzip(latestData);
                    // headers.append("Content-Encoding", "gzip");
                } else {
                    request["body"] = latestData;
                }

            }
            request["headers"] = headers;
            if (!!context.beforeFunctionList) {
                let beforePromiseChain = context.beforeFunctionList.reduce((parentPromise, func) => parentPromise.then(() => new Promise((resolve, reject) => {
                    func(context.method, assign(context.data), resolve, reject);
                })), Promise.resolve());
                beforePromiseChain.then(() => {
                    return fetch(context.apiHost + context.endPoint, request).then((response) => {
                        let afterPromiseChain = context.afterFunctionList.reduce((parentPromise, func) => parentPromise.then(() => new Promise((resolve, reject) => {
                            func(response, resolve, reject);
                        })), Promise.resolve());
                        if (context.afterFunctionList.length == 0)
                            return new Promise((resolve, reject) => resolve(response));
                        return afterPromiseChain;
                    }).catch((error) => {
                        let afterPromiseChain = context.afterFunctionList.reduce((parentPromise, func) => parentPromise.then(() => new Promise((resolve, reject) => {
                            func(null, resolve, reject);
                        })), Promise.resolve());
                        if (context.afterFunctionList.length == 0)
                            return new Promise((resolve, reject) => resolve());
                        return afterPromiseChain;
                    });
                }).then((response) => {
                    if (response.status >= 400) {
                        response.text().then(function (text) {
                            reject(new APIException(response.status, getErrorText(text)));
                        });
                        // if (response.status == 401) {
                        //     global.onInvalidToken && global.onInvalidToken()
                        // }
                    } else {
                        resolve(response);
                    }
                }).catch(error => {
                    reject(new APIException(-999, getErrorText(error)));
                });
            }
        });


    }
}

APIInvoker.dataTypeMap = {
    urlForm: "application/x-www-form-urlencoded",
    multipartForm: "multipart/form-data",
    json: "application/json"
};
