import {
    Button,
    Space,
    FloatingPanel,
    Popup,
    ConfigProvider,
    ErrorBlock,
    setDefaultConfig,
    FloatingBubble,
} from "antd-mobile";
import {useContext, useState, Component} from "react";
import BasePage from "./BasePage";

import Settings, {ColorContext, ColorType, DaynightContext, SettingsContext, ThemeContext} from './Settings'
import {Slider, Switch} from "antd";

export default class EnglishPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            switch: true,
            sliderValue: 1,
            pointA: { // 元素中心点 元素1/2自身宽高 + 元素的定位
                X: 0,
                Y: 0
            },
            pointB: {},
            pointC: {},
            typeMouse: false,
            rotateDegree: 0,
            rotateDegree_: 0,
            count: 0,
            pointA2: { // 元素中心点 元素1/2自身宽高 + 元素的定位
                X: 0,
                Y: 0
            },
            pointB2: {},
            pointC2: {},
            typeMouse2: false,
            rotateDegree2: 0,
            rotateDegree2_: 0,
            count2: 0,
        }
    }


    render() {
        let context = this
        let size = window.innerHeight - 45 - 50
        size /= 2
        let imgSize = size + size * context.state.sliderValue / 100
        return <BasePage
            naviTitle={'易演'}
            onBack={context.props.onBack}
            naviRight={undefined}
        >
            <div
                style={{
                    flex: 1, width: '100%',
                    // backgroundColor: settings.color[1],
                    overflowY: "auto",
                    display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center',
                }}
            >
                <div
                    style={{
                        width: '100%', height: 50, paddingLeft: 15, paddingRight: 15,
                        display: 'flex', flexDirection: 'row', alignItems: 'center',
                    }}
                >

                    <Slider
                        style={{
                            flex: 1, minWidth: 0,
                        }}
                        tooltip={{open: true}}
                        value={context.state.sliderValue}
                        onChange={(e) => {
                            context.state.sliderValue = e
                            context.setState({})
                        }}
                    />
                    <Switch
                        value={context.state.switch}
                        onChange={(checked) => {
                            context.state.switch = checked
                            context.setState({})
                        }}
                    />
                    <Button
                        style={{
                            marginLeft: 10,
                            width: 120,
                        }}
                        onClick={() => {
                            context.state.rotateDegree = 0
                            context.setState({})
                        }}
                    >重置加临</Button>
                    <Button
                        style={{
                            marginLeft: 10,
                            width: 60,
                        }}
                        onClick={() => {
                            context.state.rotateDegree2 = 0
                            context.setState({})
                        }}
                    >重置</Button>
                </div>
                <div
                    style={{
                        minHeight: 0,
                        flex: 1,
                        position: 'relative',
                        display: 'flex',
                        flexDirection: 'row',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                >
                    <div
                        style={{
                            position: 'absolute', top: '50%', left: '50%', width: 0, height: 0,
                            // position: 'relative',
                            display: 'flex',
                            flexDirection: 'row',
                            alignItems: 'center',
                            justifyContent: 'center',
                            // width: imgSize,
                            // height: imgSize,
                        }}
                    >
                        <img
                            style={{
                                pointerEvents: 'none !important',
                                width: imgSize,
                                height: imgSize,
                                transform: context.state.switch ? 'rotate(' + context.state.rotateDegree + 'deg)' : '',
                            }}
                            src={require('./outer2.png')}
                        />
                    </div>
                    <div
                        style={{
                            position: 'absolute', top: '50%', left: '50%', width: 0, height: 0,
                            // position: 'relative',
                            display: 'flex',
                            flexDirection: 'row',
                            alignItems: 'center',
                            justifyContent: 'center',
                            // width: imgSize,
                            // height: imgSize,
                        }}
                        onMouseDown={(e) => {
                            e.preventDefault()
                            e.stopPropagation()
                            let t = document.getElementById('outer');
                            let rect = t.getBoundingClientRect()
                            let x = {
                                top: rect.top + window.scrollY,
                                right: rect.right,
                                bottom: rect.bottom,
                                left: rect.left + window.scrollX,
                                X: rect.left + window.scrollX + (rect.right - rect.left) / 2,
                                Y: rect.top + window.scrollY + (rect.bottom - rect.top) / 2,
                            }
                            context.state.pointA = x
                            if (context.state.count < 1) { // 以鼠标第一次落下的点为起点
                                context.state.pointB.X = e.pageX;
                                context.state.pointB.Y = e.pageY;
                                context.state.count++
                            }
                            context.state.typeMouse = true
                            context.state.typeMouse2 = false
                            context.state.lastRotateDegree = context.state.rotateDegree
                            context.setState({})
                        }}
                        onMouseMove={(e) => {
                            e.preventDefault()
                            e.stopPropagation()
                            if (context.state.typeMouse) {
                                context.state.pointC.X = e.pageX;
                                context.state.pointC.Y = e.pageY; // 获取结束点坐标
                                // 计算出旋转角度
                                var AB = {};
                                var AC = {};
                                AB.X = (context.state.pointB.X - context.state.pointA.X);
                                AB.Y = (context.state.pointB.Y - context.state.pointA.Y);
                                AC.X = (context.state.pointC.X - context.state.pointA.X);
                                AC.Y = (context.state.pointC.Y - context.state.pointA.Y); // 分别求出AB,AC的向量坐标表示
                                var direct = (AB.X * AC.Y) - (AB.Y * AC.X); // AB与AC叉乘求出逆时针还是顺时针旋转
                                var lengthAB = Math.sqrt(Math.pow(context.state.pointA.X - context.state.pointB.X, 2) +
                                    Math.pow(context.state.pointA.Y - context.state.pointB.Y, 2)),
                                    lengthAC = Math.sqrt(Math.pow(context.state.pointA.X - context.state.pointC.X, 2) +
                                        Math.pow(context.state.pointA.Y - context.state.pointC.Y, 2)),
                                    lengthBC = Math.sqrt(Math.pow(context.state.pointB.X - context.state.pointC.X, 2) +
                                        Math.pow(context.state.pointB.Y - context.state.pointC.Y, 2));
                                var cosA = (Math.pow(lengthAB, 2) + Math.pow(lengthAC, 2) - Math.pow(lengthBC, 2)) /
                                    (2 * lengthAB * lengthAC); //   余弦定理求出旋转角
                                var angleA = Math.round(Math.acos(cosA) * 180 / Math.PI);
                                if (direct < 0) {
                                    context.state.rotateDegree = -angleA; //叉乘结果为负表示逆时针旋转， 逆时针旋转减度数
                                } else {
                                    context.state.rotateDegree = angleA; //叉乘结果为正表示顺时针旋转，顺时针旋转加度数
                                }
                                context.state.rotateDegree = context.state.lastRotateDegree + context.state.rotateDegree
                                context.setState({})
                                // console.log({rotateDegree})
                            }
                        }}
                        onMouseUp={(e) => {
                            e.preventDefault()
                            e.stopPropagation()
                            if (!context.state.typeMouse) return
                            context.state.typeMouse = false;
                            let t = context.state.rotateDegree
                            let m = t > 0
                            t = Math.abs(t)
                            let x = parseInt(t / 30)
                            let y = t - x * 30
                            y = y = 0 ? 0 : y < 15 ? 0 : 30
                            let f = x * 30 + y
                            f = f * (m ? 1 : -1)
                            context.state.rotateDegree_ = f
                            context.setState({})
                            context.state.count = 0
                            if (context.state.rotateDegree != context.state.rotateDegree_) {
                                context.timer && clearInterval(context.timer)
                                context.timer = setInterval(() => {
                                    context.state.rotateDegree += context.state.rotateDegree < context.state.rotateDegree_ ? 1 : -1
                                    if (context.state.rotateDegree == context.state.rotateDegree_) {
                                        context.timer && clearInterval(context.timer)
                                    }
                                    context.setState({})
                                }, 25)
                            }
                        }}
                    >
                        <img
                            style={{
                                pointerEvents: 'none !important',
                                width: imgSize,
                                height: imgSize,
                                transform: 'rotate(' + context.state.rotateDegree + 'deg)',
                            }}
                            src={require('./outer.png')}
                            id={'outer'}
                        />
                        <div
                            style={{
                                position: 'absolute', top: '50%', left: '50%', width: 0, height: 0,
                                display: 'flex',
                                flexDirection: 'row',
                                alignItems: 'center',
                                justifyContent: 'center',
                            }}
                            onMouseDown={(e) => {
                                e.preventDefault()
                                e.stopPropagation()
                                let t = document.getElementById('inner');
                                let rect = t.getBoundingClientRect()
                                let x = {
                                    top: rect.top + window.scrollY,
                                    right: rect.right,
                                    bottom: rect.bottom,
                                    left: rect.left + window.scrollX,
                                    X: rect.left + window.scrollX + (rect.right - rect.left) / 2,
                                    Y: rect.top + window.scrollY + (rect.bottom - rect.top) / 2,
                                }
                                context.state.pointA2 = x
                                if (context.state.count2 < 1) { // 以鼠标第一次落下的点为起点
                                    context.state.pointB2.X = e.pageX;
                                    context.state.pointB2.Y = e.pageY;
                                    context.state.count2++
                                }
                                context.state.typeMouse2 = true
                                context.state.typeMouse = false
                                context.state.lastRotateDegree2 = context.state.rotateDegree2
                                context.setState({})
                            }}
                            onMouseMove={(e) => {
                                e.preventDefault()
                                if (context.state.typeMouse2) {
                                    context.state.pointC2.X = e.pageX;
                                    context.state.pointC2.Y = e.pageY; // 获取结束点坐标
                                    // 计算出旋转角度
                                    var AB = {};
                                    var AC = {};
                                    AB.X = (context.state.pointB2.X - context.state.pointA2.X);
                                    AB.Y = (context.state.pointB2.Y - context.state.pointA2.Y);
                                    AC.X = (context.state.pointC2.X - context.state.pointA2.X);
                                    AC.Y = (context.state.pointC2.Y - context.state.pointA2.Y); // 分别求出AB,AC的向量坐标表示
                                    var direct = (AB.X * AC.Y) - (AB.Y * AC.X); // AB与AC叉乘求出逆时针还是顺时针旋转
                                    var lengthAB = Math.sqrt(Math.pow(context.state.pointA2.X - context.state.pointB2.X, 2) +
                                        Math.pow(context.state.pointA2.Y - context.state.pointB2.Y, 2)),
                                        lengthAC = Math.sqrt(Math.pow(context.state.pointA2.X - context.state.pointC2.X, 2) +
                                            Math.pow(context.state.pointA2.Y - context.state.pointC2.Y, 2)),
                                        lengthBC = Math.sqrt(Math.pow(context.state.pointB2.X - context.state.pointC2.X, 2) +
                                            Math.pow(context.state.pointB2.Y - context.state.pointC2.Y, 2));
                                    var cosA = (Math.pow(lengthAB, 2) + Math.pow(lengthAC, 2) - Math.pow(lengthBC, 2)) /
                                        (2 * lengthAB * lengthAC); //   余弦定理求出旋转角
                                    var angleA = Math.round(Math.acos(cosA) * 180 / Math.PI);
                                    if (direct < 0) {
                                        context.state.rotateDegree2 = -angleA; //叉乘结果为负表示逆时针旋转， 逆时针旋转减度数
                                    } else {
                                        context.state.rotateDegree2 = angleA; //叉乘结果为正表示顺时针旋转，顺时针旋转加度数
                                    }
                                    context.state.rotateDegree2 = context.state.lastRotateDegree2 + context.state.rotateDegree2
                                    context.setState({})
                                    // console.log({rotateDegree})
                                }
                            }}
                            onMouseUp={(e) => {
                                e.preventDefault()
                                if (!context.state.typeMouse2) return
                                context.state.typeMouse2 = false;
                                let t = context.state.rotateDegree2
                                let m = t > 0
                                t = Math.abs(t)
                                let x = parseInt(t / 30)
                                let y = t - x * 30
                                y = y = 0 ? 0 : y < 15 ? 0 : 30
                                let f = x * 30 + y
                                f = f * (m ? 1 : -1)
                                context.state.rotateDegree2_ = f
                                console.log(t, m, x, y, f)
                                context.setState({})
                                context.state.count2 = 0
                                if (context.state.rotateDegree2 != context.state.rotateDegree2_) {
                                    context.timer && clearInterval(context.timer)
                                    context.timer = setInterval(() => {
                                        context.state.rotateDegree2 += context.state.rotateDegree2 < context.state.rotateDegree2_ ? 1 : -1
                                        if (context.state.rotateDegree2 == context.state.rotateDegree2_) {
                                            context.timer && clearInterval(context.timer)
                                        }
                                        context.setState({})
                                    }, 25)
                                }
                            }}
                        >
                            <img
                                style={{
                                    pointerEvents: 'none !important',
                                    width: imgSize * 0.36,
                                    height: imgSize * 0.36,
                                    transform: 'rotate(' + context.state.rotateDegree2 + 'deg)',
                                }}
                                src={require('./medical.png')}
                                id={'inner'}
                            />
                        </div>
                    </div>

                </div>
            </div>
        </BasePage>
    }


}
